<template>
<div id="start">
  <div>
    <p class="title-txt">the go-to destination to remove life's stresses</p>
  </div>
  <div class="images-cont">
    <div v-for="items in pictures" :key="items.id" :class="classArray[Math.floor(Math.random()*4)]">
      <img :src="items.urls.regular" alt="peace" class="image">
    </div>
  </div>
</div>
</template>

<script>
import Showcase from './Showcase.vue'
export default {
  name: 'mapper',
  components: {
    Showcase
  },
  data() {
    return {
      pictures: [],
      classArray: ['horizontal', 'thumb', 'vertical']
    }
  },
  mounted() {
    fetch (`https://api.unsplash.com/search/photos?page=1&per_page=4&query=lake&client_id=83f0f0fa318af26ee1c0724605f47bd4e77733935726b2b489331935cb8f3eb7`)
			.then(data => data.json())
      .then(data => this.pictures = data.results)
    
  }
}
</script>

<style scoped>
  .images-cont {
    background-color: #ffffff;
    display: grid;
    grid-template-columns: repeat(3, minmax(150px, 1fr));
    grid-auto-rows: 100px;
    grid-auto-flow: dense;
    grid-gap: 0;
    margin: 50px 20vw;
    overflow: hidden;
  }
  .title-txt {
    font-family: 'Revalia', sans-serif;
    font-style: normal;
    font-weight: normal;
    font-size: large;
    display: flex;
    flex-flow: row;
    justify-content: center;
    align-items: center;
    text-align: center;
    color: #000000;
    margin: 40px 0;
  }
  .image {
    object-fit: cover;
    height: 100%;
    width: 100%;
  }
  .horizontal {
    grid-column: span 2;
  }
  .vertical {
    grid-row: span 2;
  }
  .huge {
    grid-row: span 2;
    grid-column: span 2;
  }
  .thumb {
    grid-row: span 1;
    grid-column: span 1;
  }

  @media(min-width: 2500px){
    .images-cont {
      margin: 50px 60vw;
    }
  }
  @media(min-width: 1000px){
    .images-cont {
      margin: 40px 30vw;
    }
  }
  @media(max-width: 500px) {
    .title-txt {
      font-size: 15px;
      margin: 40px 5px;
    }
    .images-cont {
      margin: 10px;
    }
  }
</style>
