<template>
  <div id="server">
    <p class="title-txt">We're here to help ...</p>
    <div class="stage">
      <div class="curtain">

      </div>
      <div class="event">
        <p class="event-txt">
          Life is hard, we know this
        </p>
      </div>
    </div>
    <div class="stage">
      <div class="event">
        <p class="event-txt" style="transition-delay: 2s;">
          Everyone needs a breather, from time to time
        </p>
      </div>
      <div class="curtain">

      </div>
    </div>
    <div class="stage">
      <div class="curtain" style="width: 17vw;">

      </div>
      <div class="event" style="width: 95%;">
        <p class="event-txt" style="transition-delay: 4s;">
          That's what we promise
        </p>
      </div>
      <div class="curtain" style="width: 17vw;">

      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: 'Description',
  mounted() {
    document.onscroll = function(){
      let rj = document.getElementById('server');
      if(document.documentElement.clientWidth < 768){
        if (window.pageYOffset > 450){
          console.log('hippie'); 
          let select = rj.querySelectorAll('.event-txt') ;
          select[0].classList.add('active');
          select[1].classList.add('active');
          select[2].classList.add('active');
        }
      }else if(document.documentElement.clientWidth > 760){
        if (window.pageYOffset > 600){
          console.log('hippie'); 
          let select = rj.querySelectorAll('.event-txt') ;
          select[0].classList.add('active');
          select[1].classList.add('active');
          select[1].classList.add('typewriter');
          select[2].classList.add('active');
        }
      }
  }
    }
}
</script>

<style scoped>
@import url('https://fonts.googleapis.com/css?family=Revalia&display=swap');
@import url('https://fonts.googleapis.com/css?family=Rhodium+Libre&display=swap');

  #server {
    margin: 50px;
  }
  .title-txt {
    font-family: 'Revalia', sans-serif;
    font-style: normal;
    font-weight: normal;
    font-size: 18px;
    line-height: 11px;
    display: flex;
    flex-flow: row;
    justify-content: center;
    align-items: center;
    text-align: center;
    color: #000000;
  }
  .stage {
    display: flex;
    flex-flow: row nowrap;
    margin: 70px 0;
  }
  .curtain {
    height: 30vh;
    width: 15vw;
    background-color: #030516;
    background-image: url('../assets/u6XKiFvtpQ0_Full.jpg')
  }
  .event {
    padding: 20px;
    width: 100%;
    display: flex;
    align-items: center;
    justify-content: center;
  }
  .event-txt {
    font-size: 1.3em !important;
    color: #0e24e9;
    font-family: 'Rhodium Libre', serif;
    opacity: 0.1;
    transition: opacity ease-in 600ms; 
  }
  .active {
    opacity: 1;
  }

  .typewriter {
    overflow: hidden;
    border-right: .15em solid orange; /* The typwriter cursor */
    white-space: nowrap; /* Keeps the content on a single line */
    margin: 0 auto; /* Gives that scrolling effect as the typing happens */
    animation: 
      typing 4s steps(40, end),
      blink-caret .5s step-end infinite;
  }
  @keyframes typing {
  from { width: 0 }
	to { width: 60% }
  }
  @keyframes blink-caret {
  from, to { border-color: transparent }
	50% { border-color: orange }
  }

  @media(min-width: 700px) {
    .event-txt {font-size: 1rem;}
  }
  @media(max-width: 500px) {
    .title-txt {
      font-size: 15px;
    }
  }
</style>