<template>
  <div class="cont">
    <p class="title-txt">Need to know even more? Send us a message:</p>
    <div>
      <input type="text" placeholder="Your message here" @click="modal" class="inpt">
    </div>
    <div class="modal" :class="{modaler: isModal}">
      <span class="close" @click="modal(); reset();">&times;</span>
      <div class="modal-div">
      <div class="modal-content" :class="{curtain: drapes}">
      <h4>Send Us a message. We'd love to hear from you!</h4>
      <label for="name">Name</label><input type="text" required v-model="name">
      <label for="msg">Message</label><textarea cols="10" required v-model="msg"></textarea>
      <button @click="exit" :disabled="disabler">Send Message</button>
      </div>
      <div class="exit" :class="{exiter: isExit}">
        <h3>Thank you, {{ name }}</h3>
        <p>We've recieved your message and will get back to you as soon
          as possible.
        </p>
      </div>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: 'Message',

  data(){
    return {
      isModal: false,
      name: "",
      isExit: false,
      drapes: false,
      msg: ""
    }
  },

  methods: {
    modal(){
      this.isModal = !this.isModal
      this.drapes = false
    },
    exit(){
      this.isExit = !this.isExit
      this.drapes = true
    },
    reset(){
      this.isExit = false
      this.name = ''
      this.msg =''
    }
  },

  computed: {
    disabler(){
      if(this.name !== ''){
        return false
      }else{
        return true
      }
    }
  }
}
</script>

<style scoped>
@import url('https://fonts.googleapis.com/css?family=Revalia&display=swap');
  .cont {
    background-color: #ffffff;
    display: flex;
    flex-flow: column nowrap;
    margin: 50px;
  }
  .title-txt {
    font-family: 'Revalia', sans-serif;
    font-style: normal;
    font-weight: normal;
    font-size: 14px;
    line-height: 11px;
    display: flex;
    flex-flow: row;
    justify-content: center;
    align-items: center;
    text-align: center;
    color: #000000;
  }
  .inpt {
    border: 1px solid rgba(3, 5, 22, 0.575);
    border-radius: 3px;
    text-align: center;
    font-family: sans-serif;
  }
  .modal {
    display: none;
    position: fixed;
    z-index: 1;
    padding-top: 100px; 
    left: 0;
    top: 0;
    width: 100%;
    height: 150%;
    overflow: auto;
    background-color: rgb(0,0,0);
    background-color: rgba(0,0,0,0.9);
    justify-content: center;
    align-content: center;
  }
  .modal-div {
    margin: 0 auto;
    width: 70vw !important;
    height: 60vh;
    animation-name: zoom;
    animation-duration: 0.6s;
    display: flex;
    flex-flow: column nowrap;
    justify-content: center;
    background-color: #FAFAFA;
    border: 1px solid #FAFAFA;
    border-radius: 5px;
  }
  .modal-content{
    display:flex;
    flex-flow: column nowrap;
    justify-content: center;
  }
  h4{text-align: center;}
  label{
    align-self: flex-end;
    margin-right: 10vw;
  }
  .modal-content>input, .modal-content>textarea{
    width: 30vw;
    align-self: flex-end;
    margin-right: 10vw;
    font-family: Arial, Helvetica, sans-serif;
  }
  .modal-content>button{
    color: white;
    background-color: #030516;
    width: 180px;
    margin: 10vh 0 0 5vw;
    border: 1px solid #030516;
    border-radius: 5px;
  }
  .modal-content>button:hover{
      background-color: #121433;
      border: 1-x solid #121433;
      cursor: pointer;
    }
  .modaler {
    display: block !important;
    display: flex !important;
  }
  .close {
    position: absolute;
    top: 15px;
    right: 35px;
    color: #f1f1f1;
    font-size: 40px;
    font-weight: bold;
    transition: 0.3s;
  }
  .close:hover,
  .close:focus {
    color: #bbb;
    text-decoration: none;
    cursor: pointer;
  }
  .exit{
    display: none;
    flex-flow: column nowrap;
    margin: auto 70px;
  }
  .exiter{
    display: block !important;
    display: flex !important;
  }
  .curtain{
    display: none !important;
  }

  @keyframes zoom {
    from {transform:scale(0)}
    to {transform:scale(1)}
  }

  @media (max-width: 500px) {
    .title-txt {
      font-size: 10px;
    }
    label {font-size: 0.8rem;}
    .modal-content {
      padding: 0 10px;
    }
    .modal-content>input, .modal-content>textarea {
      width: 70%;
    }
    .modal-content>h4, .exit>h3, .exit>p {
      font-size: 0.8rem;
    }
    .modal-content>button {
      width: auto;
      margin: 20px auto;
    }
  }
</style>