<template>
  <div class="cont">
    <p class="title-txt">testimonials ...</p>
    <div class="tes-cont">
      <div class="tes-details">
        <p>{{ currentText }}</p>
        <p>- {{ currentName }}, {{ currentPlace }}</p>
        <div>
        <button class="switch-btn" @click="prev">&#10094;</button>
        <button class="switch-btn" @click="next">&#10095;</button>
        </div>
      </div>
      <div v-for="number in [currentNumber]" :key='number' class="tes-img-cont" :class="{ active: isActive }">
        <img :src="currentImage" class="tes-img">
      </div>
    </div>
    
  </div>
</template>

<script>
import testimonials from '../assets/Testimonials.js'
import joe from '../assets/prince-akachi-4Yv84VgQkRM-unsplash.jpg';
import mary from '../assets/terricks-noah-qT_VNmNAOCY-unsplash.jpg';
import remi from '../assets/darran-shen-Q_oXnBhxVu0-unsplash.jpg';
import musa from '../assets/anaya-katlego-tQ64LAblOUU-unsplash.jpg';

export default {
  name: 'testimonial',
  data() {
    return {
      images: [joe, mary, remi, musa],
      currentNumber: 0,
      isActive: false
    }
  },
  methods: {
    next: function() {
      this.currentNumber += 1
      this.isActive = true
    },
    prev: function() {
      this.currentNumber -= 1
      this.isActive = true
    }
  },
  mounted() {
    this.showDivs(this.slideIndex);
  },
  computed: {
    currentImage: function() {
      return this.images[Math.abs(this.currentNumber) % this.images.length];
    },
    currentText: function() {
      return testimonials[Math.abs(this.currentNumber) % this.images.length].text;
    },
    currentName: function() {
      return testimonials[Math.abs(this.currentNumber) % this.images.length].name;
    },
    currentPlace: function() {
      return testimonials[Math.abs(this.currentNumber) % this.images.length].address;
    }
  }
}
</script>

<style scoped>
@import url('https://fonts.googleapis.com/css?family=ZCOOL+XiaoWei&display=swap');
@import url('https://fonts.googleapis.com/css?family=Revalia&display=swap');
  .cont {
    background-color: #ffffff;
    display: flex;
    flex-flow: column nowrap;
    margin: 50px;
  }
  .title-txt {
    font-family: 'Revalia', sans-serif;
    font-style: normal;
    font-weight: normal;
    font-size: 18px;
    line-height: 11px;
    display: flex;
    flex-flow: row;
    justify-content: center;
    align-items: center;
    text-align: center;
    color: #000000;
  }
  .tes-cont {
    display: flex;
    flex-flow: row nowrap;
    align-items: flex-end;
    width: 50%;
    margin: 20px 0 0 50%;
    height: 30vh;
  }
  .tes-img-cont {
    display: flex;
    justify-content: center;
    align-items: center;
    overflow: hidden;
    position: relative;
    width: 30%;
    height: 30vh;
    opacity: 0.8;
    transition: opacity ease-in 800ms;
  }
  .tes-img {
    height: 100px;
    width: 100px !important;
    object-fit: cover;
    vertical-align: bottom;
  }
  .active { opacity: 0.8; }
  .tes-details {
    text-align: right;
    margin: 0 10px;
    position: relative;
    width: 70%;
    display: flex;
    flex-flow: column nowrap;
    justify-content: center;
    font-family: 'ZCOOL XiaoWei', serif;
    font-weight: 450;
    color: #0f0f0f;
  }
  .switch-btn {
    color: #ffffff;
    background-color: #333333;
    border: 1px solid #333333;
    outline: none;
    cursor: pointer;
    height: 7vh;
    width: 4vw;
  }
  .switch-btn:hover {
    background-color: #0f0f0f;
    border: 1px solid #0f0f0f;
  }

  @media(max-width: 800px) {
    .tes-details {
      font-size: 12px;
    }
    .tes-img {
      height: 170px;
      width: 160px !important;
      object-fit: cover;
    }
  }
  @media(max-width: 500px) {
    .title-txt {
      font-size: 15px;
    }
    .tes-details {
      font-size: 10px;
    }
    .cont {
      margin: 20px;
    }
    .tes-cont {
      margin-left: 30%;
      width: 70%;
    }
    .tes-img {
      height: 100px;
      width: 160px !important;
      object-fit: cover;
    }
    .switch-btn { width: auto; height: 5vh; }
  }
</style>