<template>
  <div class="header" :style="{'position':position, 'background-color':background}">
    <div>
      <img :src="navLogo" class="logo">
    </div>
    <div class="nav">
      <p class="nav-link" :style="{'color':navTxt}"><router-link :to="linkTo">{{linkTxt}}</router-link></p>
      <p class="nav-link" :style="{'color':navTxt}"><router-link :to="linkTo2">{{linkTxt2}}</router-link></p>
    </div>
  </div>
</template>

<script>
import '@/styles/Header.css'
import logoWhite from '../assets/Logo.svg';
import logoBlack from '../assets/Logo-black.svg';

export default {
  name: 'header',
  data() {
    return {
      navLogo: String,
      navTxt: String,
    }
  },
  props: ['fontClr', 'logoType', 'position', 'background','linkTo','linkTxt','linkTo2','linkTxt2'],
  mounted() {
    if(this.logoType === 'black'){ this.navLogo = logoBlack; this.navTxt = 'black'; }
    else { this.navLogo = logoWhite; this.navTxt = 'white'; }
  }
}
</script>